import React, { Component } from "react";
import "./App.css";
import { Button } from "./components/button";
import { Input } from "./components/input";
import { ClearButton } from "./components/clearButton";
import * as math from "mathjs";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      input: ""
    };
  }

  addToInput = val => {
    this.setState({ input: this.state.input + val });
  };

  handleEqual = () => {
    this.setState({ input: math.eval(this.state.input) });
  };

  render() {
    return (
      <div className="app">
        <div className="calc-wrapper">
          <Input input={this.state.input}></Input>
          <div className="row">
            <Button handleClick={this.addToInput} children={7} />
            <Button handleClick={this.addToInput} children={8} />
            <Button handleClick={this.addToInput} children={9} />
            <Button handleClick={this.addToInput} children={"/"} />
          </div>
          <div className="row">
            <Button handleClick={this.addToInput} children={4} />
            <Button handleClick={this.addToInput} children={5} />
            <Button handleClick={this.addToInput} children={6} />
            <Button handleClick={this.addToInput} children={"*"} />
          </div>
          <div className="row">
            <Button handleClick={this.addToInput} children={1} />
            <Button handleClick={this.addToInput} children={2} />
            <Button handleClick={this.addToInput} children={3} />
            <Button handleClick={this.addToInput} children={"+"} />
          </div>
          <div className="row">
            <Button handleClick={this.addToInput} children={"."} />
            <Button handleClick={this.addToInput} children={0} />
            <Button handleClick={() => this.handleEqual()} children={"="} />
            <Button handleClick={this.addToInput} children={"-"} />
          </div>
          <div className="row">
            <ClearButton handleClear={() => this.setState({ input: "" })}>
              Clear
            </ClearButton>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
