import React from "react";
import "./button.css";

// this complicated mess right here is used to check for normal numerical values including the dot and equal to buttons. this would enable us stryle the operator buttons differently
const isOperator = val => {
  return !isNaN(val) || val === "." || val === "=";
};

export const Button = props => (
  <div
    className={`button-wrapper ${
      isOperator(props.children) ? null : "operator"
    }`}
    onClick= {() => props.handleClick(props.children)}
  >
    {props.children}
  </div>
);
